.SuperCheckbox_label__3B0xg > input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

/* для элемента label, связанного с .custom-checkbox */
.SuperCheckbox_label__3B0xg > span {
  display: inline-flex;
  align-items: center;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 4px 2px;
}

/* создание в label псевдоэлемента before со следующими стилями */
.SuperCheckbox_label__3B0xg > span::before {
  content: '';
  display: inline-block;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  flex-grow: 0;
  border: 2px solid #a9a9aa;
  border-radius: 0.35em;
  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}

/* стили при наведении курсора на checkbox */
.SuperCheckbox_label__3B0xg > input:not(:disabled):not(:checked) + span:hover::before {
  border-color: #2c1f1f;
}

/* стили для активного чекбокса (при нажатии на него) */
.SuperCheckbox_label__3B0xg > input:not(:disabled):active + span::before {
  background-color: #252121;
  border-color: #626265;
}

/* стили для чекбокса, находящегося в фокусе */
.SuperCheckbox_label__3B0xg > input:focus + span::before {
  box-shadow: 0 0 0 0.2rem rgba(49, 45, 45, 0.82);
}

/* стили для чекбокса, находящегося в фокусе и не находящегося в состоянии checked */
.SuperCheckbox_label__3B0xg > input:focus:not(:checked) + span::before {
  border-color: #0e0e0e;
}

/* стили для чекбокса, находящегося в состоянии checked */
.SuperCheckbox_label__3B0xg > input:checked + span::before {
  border-color: #4f4e4d;
  background-color: #2c3c41;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
}

/* стили для чекбокса, находящегося в состоянии disabled */
.SuperCheckbox_label__3B0xg > input:disabled + span::before {
  background-color: #e9ecef;
}

